export default {
  data(){
    return {
      array : {}
    }
  },
  mounted:function(){
    this.$nextTick(function() {
      this.Init();
    })
  },
  methods:{
    Init: function() {
      var _this = this;
      $.get('index.php?c=Index&a=getArticleList', null, function(ret) {
        if (ret) {
          var $array  = {};
          for (var i = 0; i < ret.length; i++) {
              var year =  ret[i].create_time.substr(0,4);
              if($array[year] == undefined){
                  $array[year] = [{
                    time: ret[i].create_time.substr(5,5),
                    title: ret[i].title,
                    bornum:ret[i].browse_num
                  }]
              } else {
                $array[year].push ({
                  time: ret[i].create_time.substr(5,5),
                  title: ret[i].title,
                  bornum:ret[i].browse_num
                })
              }
          }
          setTimeout(function() {
            $('.loader-mask').css('display', 'none');
          }, 900);
          _this.array = $array;
        }
      }, "JSON");
    },
  },
}



