import Content from '../components/Common/Contents.vue'
import NewsFollowRecommend from '../components/Common/NewsFollowRecommend.vue'

export default {
  data() {
    return {
      contentData:[]
    }
  },
  components:{
     NewsFollowRecommend,
     Content
  },
  mounted: function() {
    var _this = this;
    _this.Init();
  },
  computed: {

  },
  methods: {
    Init: function() {
      var _this = this;
      $.get('index.php?c=Index&a=getTypeArticleList', {type_name: '慢生活'}, function(ret) {
        if (ret) {
          for (var i = 0; i < ret.length; i++) {
            setTimeout(function() {
              $('.loader-mask').css('display', 'none');
            }, 600);
            _this.contentData.push(ret[i]);
          }
        }
      }, "JSON");
    },
  },
}



